import React, { useState } from "react";
import "./Admin.scss";

import ActualityForm from "../components/ActualityForm";

const Admin = () => {
  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");
  // pepna pokud je autorizace sprvn nebo patn
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  // Uloen jmna a hesla z formule
  const writeName = (e) => {
    setUserName(e.target.value);
  };
  const writePassword = (e) => {
    setPassword(e.target.value);
  };

  // Pihlen
  const login = (e) => {
    e.preventDefault();

    // Heslo
    const adminPassword = "admin";
    const adminName = "admin";

    // Oveen hesla a jmna
    if (password === adminPassword && userName === adminName) {
      setIsAuthenticated(true);
    } else {
      alert("Nesprvn Heslo. Jmno i Heslo jsou 'admin'");
    }
  };

  // Pokud nen ovena autorizace piprav formul

  if (!isAuthenticated) {
    // po odesln zavolat pihlaovac funkci
    return (
      <form className="login-admin-page" onSubmit={login}>
        <input type="text" placeholder='jmno "admin"' onChange={writeName} />
        <input
          type="text"
          placeholder='heslo "admin"'
          onChange={writePassword}
        />
        <input type="submit" />
      </form>
    );
  }

  return <ActualityForm />;
};

export default Admin;
