import animals from "../data/animals";
import { useParams, Link } from "react-router-dom";
import "./OneZvire.scss";
import SocialMedia from "../components/SocialMedia";
import React, { useEffect, useState } from "react";
import IconBack from "../images/icon/back.png";

const OneZvire = () => {
  const { zvirataId } = useParams();
  const [similarAnimals, setSimialrAnimals] = useState([]);

  // Vyfiltrovn sprvn zvete do strnky
  const animal = animals.find((one) => {
    return one.id === parseInt(zvirataId);
  });

  // Podobn duruhy filter
  useEffect(() => {
    const similarKind = animals.filter((one) => one.kind === animal.kind);
    setSimialrAnimals(similarKind);
  }, [animal.kind]);

  // Funkce pro vrcen strnky zpt nahoru
  const ScrollTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  return (
    <div className="specific-animal">
      <div className="one-animal-container row3">
        <div className="heading">
          <h1>{animal.name}</h1>
          <Link to="/zvirata">
            <img src={IconBack} alt="" />
          </Link>
        </div>

        <div className="about-animal-container">
          <img src={animal.url} alt="" />
          <div className="about-descritpion">
            <article>Zkladn info:</article>
            <p>{animal.description_short}</p>
            <ul>
              <p>
                Potrava: <li>{animal.food}</li>
              </p>
              <p>
                Vha: <li>{animal.mass}</li>
              </p>
              <p>
                Vka: <li>{animal.height}</li>
              </p>
              <p>
                Dlka: <li>{animal.length}</li>
              </p>
              <p>
                Rychlost: <li>{animal.max_speed}</li>
              </p>
              <p>
                Vk: <li>{animal.max_age}</li>
              </p>
            </ul>
          </div>
        </div>
        <div className="media-animal">
          <SocialMedia />
        </div>
        <h2> Pette si vice o {animal.name}</h2>
        <div className="long-description">
          <div>
            <h3>Msto vskytu</h3>
            <p>{animal.occurrence}</p>
          </div>
          <div>
            <h3>Poet Mlat</h3>
            <p>{animal.number_of_young}</p>
          </div>
          <div>
            <h3>Bezost</h3>
            <p>{animal.gestation_period}</p>
          </div>
          <div>
            <h3>Srst/Ke</h3>
            <p>{animal.coat_type}</p>
          </div>
          <div>
            <h3>Rozmnoovn</h3>
            <p>{animal.reproduction}</p>
          </div>
          <h3>Popis</h3>
          <p>{animal.description_long}</p>
        </div>

        <div className="similar-kind">
          <h2>Dal {animal.kind} v na zoo:</h2>
          <div className="similiar-container">
            {similarAnimals.map((one) => {
              return (
                <Link
                  onClick={() => ScrollTop()}
                  to={`/zvirata/${one.id}`}
                  key={one.id}
                  className="similar-box"
                  style={{
                    background: `url( ${one.url})`,
                    backgroundPosition: "center",
                    backgroundSize: "cover",
                  }}
                >
                  <div className="box">
                    <h2>{one.name}</h2>
                  </div>
                </Link>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default OneZvire;
