        description_long: "Gibon bloocas (Hylobates albibarbis) je druh primta, kter pat do eledi hominid. Giboni jsou mal opice, kter se vyskytuj v jihovchodn Asii. Giboni jsou charakterizovni svou elegantn postavou, dlouhmi konetinami a zvltnm pohybem, kter se nazv brachiace. Gibon bloocas je znm svm svtlm a hustm koichem, kter je na hrudi bl, odtud tak pochz jeho jmno. Ocas gibona bloocasho je relativn krtk a bv asto skryt pod srst. Jejich obliej je zpravidla ern nebo tmav barvy. Tito primti ij pevn ve stromovm prosted, kde se pohybuj pomoc brachiace, co je schopnost pohybovat se z jednoho stromu na druh pomoc ramen a pednch konetin. Giboni jsou zrun akrobat a mohou se pohybovat mezi stromy rychlost a 55 kilometr za hodinu. Giboni bloocas jsou veravci, jejich strava se skld z rznch plod, list, kvt a hmyzu. Jsou to teritoriln zvata a komunikuj pomoc vraznho zpvu, kter slou k obran svho zem a navazovn kontaktu s ostatnmi jedinci Tento druh je ohroen kvli ztrt pirozenho prosted, odlesovn a pytlctv. Rzn organizace se sna chrnit gibony bloocas a jejich pirozen prosted prostednictvm ochrannch program a osvty. Giboni bloocas jsou fascinujc tvorov s jedinenm zpsobem pohybu a komunikace. Jejich zachovn je dleit pro udren biodiverzity a ekologick rovnovhy v lesnch ekosystmech jihovchodn Asie.",
        food: "Ovoce, listy, kvty, vhonky a hmyz.",
