        max_age: "15-20 let",
        description_short: "Lemur je stedn velk primt s dlouhm ocasem a charakteristickm obliejovm vzorem.",
        description_long: "Lemurov jsou skupinou primt, kte jsou endemickmi obyvateli Madagaskaru. Tito uniktn tvorov maj sv vlastn jedinen rysy a adaptace, kter je odliuj od ostatnch primt. Lemurov maj rzn velikosti a tvary, od mench lemur s vhou kolem 30 gram a po vt druhy dosahujc hmotnosti a 10 kilogram. Maj velmi vrazn oi, kter jim umouj non aktivitu a dobrou viditelnost ve eru. Jejich srst je obvykle hust a me mt rzn barvy a vzory, od hnd a ed po ziv ervenou nebo ernou. Lemurov jsou znm svou schopnost skkat a pohybovat se mezi stromy pomoc jejich silnch zadnch konetin a dlouhch ocsk. Nkter druhy maj tak schopnost obratnho chze po vech tyech konetinch, co jim umouje rychl a pohybliv pohyb v korunch strom. Strava lemur se li v zvislosti na druhu, ale obecn jsou to veravci. Nkte se iv ovocem, kvty a nektarem, zatmco jin lov hmyz, mal obratlovce nebo se iv listy a krou strom. Nkter druhy maj tak specifickou stravu, jako napklad bambus nebo mstn plody. Lemurov maj sociln strukturu, kter se li podle druhu. Nkter ij v mench rodinnch skupinch, zatmco jin vytvej vt komunitn skupiny. Komunikuj mezi sebou pomoc zvuk, pohyb a pachovch signl. Lemurov jsou ohroeni kvli ztrt pirozenho prosted, odlesovn, lovem a neleglnm obchodem. Madagaskar je jedinenm ostrovem s velkou biodiverzitou, a ochrana lemur je klov pro udren ekosystm a zachovn prodnho bohatstv ostrova. Rzn organizace a vzkumn skupiny se angauj v ochran lemur a jejich pirozenho prosted prostednictvm ochrannch program, vzdlvn a ekoturistiky. Dleit je tak povdom veej",
