        id:12,
        name: "Lemur ern",
        max_age: "20-25 let",
        description_short: "Lemur ern je stedn velk primt s ernou srst a charakteristickmi oranovmi oima.",
        description_long: "Lemur ern (Eulemur macaco), tak znm jako ern lemur nebo kpov lemur, je druh lemur, kter se vyskytuje na Madagaskaru. Je to stedn velk lemur s charakteristickm ernm koichem, kter pokrv jeho tlo, a oranovmi odstny na oblieji a konetinch. Tito lemui maj pomrn variabiln strukturu skupin. Nkter populace ij v mench rodinnch skupinch, zatmco jin se organizuj do vtch skupin s hierarchi jedinc. Sociln interakce a komunikace hraj dleitou roli v jejich ivot, a to prostednictvm vokalizac, vizulnch signl a pachov komunikace. Co se stravy te, lemurov ern jsou veravci a jejich strava se skld pevn z ovoce, list, kvt, nektaru a rznch druh hmyzu. Jsou tak znmi tm, e konzumuj latex z uritch strom, kter obsahuje jedovat ltky, kter jim pomhaj pi boji s parazity. Lemurov ern jsou endemickm druhem Madagaskaru a el mnoha hrozbm. Ztrta pirozenho prosted, odlesovn, kcen strom a lovectv jsou hlavnmi faktory ohroujcmi jejich peit. Dleitm krokem k jejich ochran je ochrana jejich pirozenho prosted a podpora udritelnch zpsob obivy pro mstn komunity. Nkter rezervace a chrnn zem na Madagaskaru slou jako toit pro lemury ern a poskytuj jim ochranu ped neleglnm lovectvm a nienm prodnch stanovi. Rzn vzkumn programy tak pispvaj k lepmu porozumn tomuto druhu a jeho ekologii. Lemur ern je fascinujcm druhem s jedinenmi adaptacemi a socilnmi interakcemi. Jeho zachovn je dleit pro udren biodiverzity Madagaskaru a ochranu tohoto ohroenho druhu.",
