        description_long: "Lev berbersk (Panthera leo leo), znm tak jako severoafrick lev, je poddruh lva, kter obv severn a zpadn Afriku. Je to jedna z nejvtch kokovitch elem a jejich majesttn vzhled a sla ho in ikonickm symbolem divoiny. Lev berbersk je charakterizovn robustnm tlem a mohutnou hlavou s vraznou hvou u samc. Samci maj obvykle vt tlesnou hmotnost ne samice a jejich hva jim slou jako znak dominance a vizuln pitalivost pi soubojch o teritorium a samice. Tito lvi ij v rznch typech prosted, vetn savan, step, kovin a horskch oblast. Jsou pizpsoben ivotu v teplch a suchch podmnkch severn Afriky. Jejich strava se skld pevn z velkch savc, jako jsou antilopy, zebr, gazely a jin koistn druhy, kter se vyskytuj v jejich oblasti. Lev berbersk je spoleensk tvor, kter ije ve smekch tvoench samcem, samikami a jejich potomky. Smeka je hierarchicky organizovan, piem samec zaujm vedouc pozici. Samice se podlej na lov a pi o mlata. Mlad lvi se u od svch rodi loveckm dovednostem a socilnm interakcm. Bohuel, lev berbersk je ohroenm druhem. V minulosti byli loveni pro lov a tak pro obchod se lvmi stmi, kter byly povaovny za luxusn zbo. Dle jim hroz ztrta pirozenho prosted kvli odlesovn, fragmentaci jejich teritori a konfliktm s lidskmi osadami. Rzn organizace, vetn mezinrodnch ochranskch skupin a vld, se sna chrnit a obnovit populace lev berberskch. Ochrann opaten zahrnuj vyhlen chrnnch oblast, vzdlvac programy, omezen obchodu se lvmi stmi a snahy o udriteln zen divokch populac. Lev berbersk je nejen symbolikou sly a krsy africk divoiny, ale tak dleitm ekosystmovm predtorem. Je nez",
