        description_long: "Levhart nsk (Panthera pardus japonensis), znm tak jako amursk levhart, je poddruh levharta, kter obv pedevm severovchodn nu a rusk Dln vchod. Je to velk kokovit elma, kter je adaptovna na ivot v chladnch a zasnench podmnkch. Levhart nsk m thl tlo a krtk nohy, co mu umouje pohybovat se obratn a tie ve svm prosted. Jeho srst je hust a zbarven vraznmi skvrnami, kter mu poskytuj efektivn kamufl v lesnatch oblastech. Jejich ocasy jsou relativn dlouh a pomhaj jim udrovat rovnovhu pi pohybu po stromech. Tito levharti jsou specializovan lovci a jejich strava se skld pevn z rznch druh savc, jako jsou jeleni, prasata, zajci a dal men druhy. Jsou znm svou schopnost plit se ke koisti a vyuvat pekvapen a rychlosti pi lov. Levhart nsk je samotsk tvor a vtinou se setkv s ostatnmi jedinci pouze bhem pen. Samice rod obvykle jedno a ti mlata, kter zstvaj s nimi a do dosplosti. Mlata se u loveckm dovednostem a postupn se osamostatuj. Tento poddruh je ohroen a el mnoha hrozbm. Hlavnm faktorem ohroujcm jejich peit je ztrta pirozenho prosted v dsledku odlesovn a rozvoje lidskch osad. Lov pro jejich vzcnou koeinu byl tak velkm problmem v minulosti. Mezinrodn organizace a ochransk skupiny se sna chrnit levharty nsk a jejich pirozen prosted prostednictvm vyhlen chrnnch oblast, osvty a vzdlvn, ochrany prodnch rezervac a vzkumu tto ohroen populace. Levhart nsk je jedinenm a cennm druhem, kter je dleit pro zachovn biodiverzity a ekologick rovnovhy v oblastech, kde ije. Je nezbytn pokraovat v",
