        description_long: "Levhart snn (Panthera uncia), znm tak jako irbis, je velk elma, kter obv horsk oblasti Stedn Asie, vetn Himlaje a Tibetsk nhorn ploiny. Je to jedin druh kokovit elmy, kter se pizpsobil ivotu v extrmn chladnch podmnkch ve vysokch horch.  Levhart snn je charakteristick svou hustou a blou srst, kter mu poskytuje skvlou kamufl ve snhu. Je to adaptace na ivot v alpskch oblastech, kde se pohybuje a lov svou koist. Jeho tlust srst a masivn tlapky mu pomhaj udrovat teplo a zlepuj jeho pohyb ve snhu. Tito levharti maj siln tlo, dlouh ocas a krtk nohy. Jsou skvl hort lovci a jejich strava se skld pedevm z kopytnk, jako jsou bharalov (mod ovce), ibexi, gazely a jeleni. Jsou tak schopni lov mench savc a ptk, kter se vyskytuj v jejich prosted. Levhart snn je samotsk tvor a vtinou se setkv s ostatnmi jedinci pouze bhem pen. Samice rod obvykle dv a ti mlata, kter jsou vjimen roztomil s hustou srst a postupn se u lovit a pet v drsnch podmnkch svho prosted. Tento druh je povaovn za zraniteln a je ohroen ztrtou pirozenho prosted a lovectvm. Zmny klimatu, odlesovn a ruen jejich teritori lidskou innost maj negativn dopad na populaci levhart snnch. Mezinrodn organizace, vdeck spoleenstv a ochransk skupiny se sna chrnit levharty snn a jejich prosted prostednictvm ochrany prodnch rezervac, vyhlen chrnnch oblast, monitorovn populace a vzkumu jejich ekologie a chovn. Levhart snn je fascinujc a vzcn druh, kter je symbolem krsy a adaptace na extrmn podmnky vysokch hor. Je dleit pro zachovn biodiverzity a ekologick rovnovhy v oblastech.",
