        description_long: "Mravenenk velk (Formica rufa) je druh mravence, kter se vyskytuje v lesnch a travnatch oblastech Evropy, vetn st Severn Ameriky. Je to spoleensk hmyz, kter vytv sloit kolonie s tisci jedinci. Mravenenk velk je charakterizovn tmav hndm a ervenohndm zbarvenm. Jeho tlo je robustn, akoli samice jsou obvykle vt ne samci. Maj thl tla, est nohou a vrazn tykadla, kter slou k vnmn prosted. Tyto mravence najdeme v mnoha typech prosted, jako jsou lesy, louky a zahrady. Jejich hnzda se nazvaj mravenit a jsou asto umstna v pd, pod kameny nebo v dutinch strom. Mravenenk velk je znm svou schopnost tvoit rozshl hnzdn systmy, kter se skldaj z mnoha komor a chodeb. V rmci kolonie existuje hierarchie, ve kter krlovna pedstavuje reprodukn jedinec a samci se podlej na rozmnoovn. Vtina ostatnch mravenc v kolonii jsou samiky pracovnice, kter se staraj o hnzdo, zskvaj potravu, pi o larvy a brn zem ped nepteli. Mravenenk velk je vznamnm ekosystmovm hrem, protoe ovlivuje strukturu lesnho podrostu a pispv k recyklaci organickho materilu. Jeho potrava se skld z nektaru, pylu, hmyzu a dalch drobnch ivoich. Tato druh je adaptabiln a odoln, avak mnoh populace mravenenk velkch el rznm hrozbm. Ztrta pirozenho prosted, pesticidy, zmny klimatu a invazivn druhy jsou hlavnmi faktory, kter ohrouj jejich peit.  Ochrana mravenenk velkch zahrnuje zachovn jejich pirozenho prosted, minimalizaci pouvn pesticid a vzdlvn veejnosti o vznamu tchto hmyzch druh pro ekosystmy. Mravenenk velk je fascinujc tvor, kter pin mnoho pnos pro produ.",
