        description_short: "Nyala ninn je stedn velk antilopa s charakteristickm zbarvenm a prodlouenmi rohy u samc.",
        description_long: "Nyala ninn (Tragelaphus angasii) je stedn velk antilopa, kter se vyskytuje pevn v oblastech jin a vchodn Afriky. Je to elegantn a pestr antilopa, kter je znm svm vraznm pohlavnm dimorfismem, co znamen, e samec a samice se znan li svm vzhledem. Samec nyaly ninn je vt a majesttnj ne samice. M tmav hnd a ern zbarven s blmi pruhy na bocch, bichu a ocasu. Na hlav m vrazn bl pruhy a mohutn zakiven roh na ele. Samice jsou men a maj svtlej zbarven, piem jejich pruhy jsou mn vrazn. Nyaly ninn obvaj rzn typy prosted, vetn les, savan a kovinatch oblast v blzkosti vodnch zdroj. Jsou pizpsoben ivotu ve vysok vegetaci a dobe se skrvaj ped predtory, jako jsou lvi a hyeny. Jejich strava se skld pevn z list, vhonk, trvy a dal vegetace. Samec nyaly ninn je teritoriln a brn sv zem ped ostatnmi samci. Samice ij v mench skupinkch s mlaty. Bezost u nyaly ninn trv piblin 7 msc a obvykle se rod jedno mld. Mlata jsou zpotku skryt v hust vegetaci a matka se o n peliv star. Nyala ninn je populrn divok zve pro pozorovn pi safari. Jej krsn zbarven a elegantn pohyb ji in oblbenou turistickou atrakc v nrodnch parcch a rezervacch v Africe. I kdy nyala ninn nen v souasn dob povaovna za ohroen druh, existuj urit hrozby pro jej populaci. Mezi n pat ztrta pirozenho prosted v dsledku odlesovn a rozvoje lidskch osad, lovectv a pytlctv. Ochrann opaten, vetn vyhlen chrnnch oblast a udritelnho zen divokch populac.",
        food: "Trva, listy, vhonky a plody.",
        occurrence: "Obv lesy a pastviny v jin a vchodn Africe.",
