        description_long: "Panda erven (Ailurus fulgens) je ohroen druh savce, kter se vyskytuje v horskch oblastech vchodn Asie, pedevm v n, Neplu a Myanmaru. Je to mal a pvabn medvdek s charakteristickou ervenohndou srst, tmavm pruhovnm na hbet a blmi znaky kolem o. Pandy erven se iv pevn bambusem, kter tvo vtinu jejich stravy. Jsou specializovan na konzumaci rznch druh bambusu a maj vyvinut klnovit zuby, kter jim pomhaj rozdrvit a trvit tuh rostlinn materily. Obas tak jed ovoce, hmyz a dal drobn ivoichy. Jsou to samotsk zvata a vyhledvaj si stran v hustm porostu bambusu. Pandy erven jsou vbornmi lezci a trv vtinu svho asu na stromech. Dky jejich pohyblivm kloubm a pizpsobivm tlapm jsou schopny se plhat a pohybovat se ve vtvch s velkou obratnost. Kvli ztrt pirozenho prosted a odlesovn jsou pandy erven ohroeny vyhubenm. Dle je ohrouje nelegln obchod se srst a kcen bambusovch les. Mezinrodn organizace a vldy se sna chrnit pandy erven tm, e zakldaj chrnn zem, podporuj vzkum a zavdj programy na ochranu jejich pirozenho prosted. Pandy erven maj tak vznam v ochran ekosystmu, protoe jsou klovmi rozptylovai semen bambusu a pispvaj k regeneraci les. Jsou symbolem ochrany ohroench druh a popularitou se t jako ikonick zve celosvtov. Je dleit, aby byly pijaty dal opaten na ochranu pand ervench a jejich pirozenho prosted. Vzdlvn veejnosti o dleitosti zachovn biodiverzity a udritelnho rozvoje je klov pro budoucnost tchto ndhernch zvat.",
