        description_long: "Omlouvm se, ale panda velk, znm tak jako panda obrovsk (Ailuropoda melanoleuca), je pli rozshlm tmatem na to, abych napsal o n dostaten podrobn lnek bez pouit odstavc. Panda velk je ikonick druh medvda, kter se vyskytuje pouze v horskch oblastech centrln ny. Je znm svou ernoblou srst, kulatmi tvemi a pvabnm vzhledem. Pandy velk jsou pevn bloravci a jejich hlavn potravou je bambus. Jsou specializovan na konzumaci rznch druh bambusu a maj anatomick adaptace, jako siln elisti a svalnat trvic systm, kter jim umouj strvit tuhou rostlinnou stravu. Krom bambusu se v jejich jdelnku objevuj tak ovoce, koeny, vhonky a nkdy i hmyz. Pandy velk jsou solitrn zvata a maj velk zem, kter si brn. Jsou vynikajcmi lezci a trv vtinu svho asu na stromech nebo ve vkch, kde konzumuj bambusov vhonky. Jsou pomal a klidn tvorov, ale pi ohroen dokou bt agresivn a brnit se. Tito medvdi jsou vznamnm symbolem ochrany prody a jsou oznaovni za kriticky ohroen druh. Ztrta pirozenho prosted, nien les a nelegln lov pedstavuj hlavn hrozby pro jejich peit. Organizace na ochranu prody spolupracuj s nskou vldou na zchran pand velkch tm, e zakldaj chrnn zem, provdj vzkum a podporuj reprodukci v zajet. Reprodukce pand velkch je sloit a komplikovan. Samice jsou pipraveny na pen jen nkolik dn v roce a tak bezost trv piblin pt msc. Samice rod jedno, velmi mal mld, kter zpotku je slep a bezsrst. Matka se o mld star a chrn ho a do doby, kdy je dostaten siln.",
