        description_long: "Slon indick (Elephas maximus) je jednm z nejvtch suchozemskch ivoich na na planet a zrove jednm z nejohroenjch druh. Tito majesttn tvorov se vyskytuj v rznch stech Asie, vetn Indie, Neplu, Bhtnu, Banglade a Sr Lanky. Sloni indit maj robustn stavbu tla a mohutn nohy. Jsou pokryti edou k, kter je na nkolika mstech zhybech a rzn pokryta malmi chlupy. Jejich nejcharakteristitjm znakem jsou jejich dlouh kly, kter jsou ve skutenosti prodlouenmi ezky. Samci maj tak vrazn choboty, kter jsou jejich multifunknm nstrojem pro zachycovn potravy, pit, komunikaci a dal innosti. Sloni indit jsou bloravci a jejich strava se skld pevn z trvy, list, vhonk a kry strom. Jsou znm svou schopnost konzumovat velk mnostv potravy, a to a nkolik set kilogram denn. Kvli sv velikosti a poteb zskat dostatek potravy mus sloni indit t v prostornch lesch, kde maj dostatek vegetace.  Sloni indit jsou spoleensk zvata a ij v rozshlch rodinnch skupinch nazvanch stda. Stdo se obvykle skld z nkolika samic, jejich potomk a vedenho sameka. Mlata zstvaj u svch matek a do dosplosti, co je obvykle kolem deseti let. Komunikace mezi jednotlivmi leny stda probh pomoc hlasovch projev, tlesnho doteku a vizulnch signl. Bohuel, sloni indit jsou vn ohroeni vyhubenm v dsledku neleglnho lovu, ztrty pirozenho prosted a konflikt s lidmi. Obchod se slonovinou je tak velkou hrozbou pro jejich peit. Natst se v poslednch letech vyvinulo mnoho sil a iniciativ na ochranu slon indickch. Vytvej se chrnn zem, provd se vzkum, organizuj se osvtov programy.",
