        description_long: "Slon africk (Loxodonta africana) je nejvt suchozemsk ivoich na svt a ikonick symbol divok africk prody. Tito impozantn tvorov se vyskytuj v rznch oblastech subsaharsk Afriky, vetn nrodnch park, savan, bain a les.  Sloni africt maj velk, masivn tlo s mohutnmi nohami a vraznm hrbem na zdech. Jsou pokryti hrubou edou k, kter je zhyby a vrskami. Oba pohlav maj velk, zakiven slon kly, kter slou k obran, vyhrabvn koist a manipulaci s pedmty. Sameci maj tak vrazn vt kly ne samice. Sloni africt jsou bloravci a jejich strava se skld pevn z trvy, list, vtv, kry a plod strom. Jsou znm svou schopnost spotebovat obrovsk mnostv potravy denn, co vyaduje rozshl pastviny a pstup k vodnm zdrojm. Sloni jsou tak dleitmi tvrci ivotnho prosted, protoe jejich pohyb a konzumace vegetace ovlivuj krajinu. Sloni africt ij ve velkch rodinnch skupinch nazvanch stda, kter se skldaj z nkolika samic, mlat a vedoucho samce. Tato stda maj hierarchickou strukturu, kde dominantn samec m vt kontrolu nad stdem. Komunikace mezi slony probh pomoc hlasovch projev, infrazvuk, tlesnho doteku a vizulnch signl, jako jsou pohyb u a ocasu. Sloni africt jsou vn ohroeni vyhubenm v dsledku neleglnho lov pro slonovinu, nien jejich pirozenho prosted, konflikt s lidmi a zmn klimatu. V poslednch letech byly pijaty opaten k jejich ochran, jako je zizovn chrnnch oblast, vzdlvn a informovn veejnosti o dleitosti ochrany slon, a sil o snen neleglnho obchodu se slonovinou. Sloni africt jsou jedinenmi a fascinujcmi tvory.",
