        description_short: "Surikata je mal savec pipomnajc sysla, kter ije v savanch Afriky.",
        description_long: "Surikata, znm tak jako surikata pestr (Suricata suricatta), je mal druh elmice ijc v jin Africe, zejmna v poutnch oblastech Namibie, Botswany, Jihoafrick republiky a Angoly. Surikaty jsou proslul svm spoleenskm chovnm a charakteristickm vzhledem. Surikaty maj thl tlo, dlouh ocas a krtk nohy. Jsou pokryty krtkou srst, kter m pevn svtl odstn s tmavmi pruhy na zdech. Maj piatou tv s ernma oima, drobnma uima a dlouhmi vousy. Surikaty jsou adaptovan na ivot v poutnm prosted a maj schopnost snet vysok teploty a nedostatek vody. Surikaty jsou spoleensk zvata a ij ve velkch skupinch nazvanch kolonie. Tyto kolonie mohou tat a nkolik destek jedinc. Maj vyvinut systm spoluprce a kooperace, kter jim pomh pi hledn potravy a obran proti predtorm. Surikaty jsou tak znm svm systmem strnch pozic, kdy nkte jedinci vykonvaj dobrovolnou str nad ostatnmi, zatmco ti ostatn se iv nebo odpovaj. Strci vydvaj varovn signly v ppad nebezpe. Surikaty jsou masoravci a jejich strava se skld pevn z hmyzu, jako jsou termity, vbi a larvy. Jsou tak schopni lovit men obratlovce, napklad jetrky, mal hady nebo hlodavce. Surikaty maj dobe vyvinut zrak a ich, kter jim pomhaj najt potravu i pesto, e ij v poutnm prosted s omezenmi zdroji. Surikaty jsou velmi aktivn a neustle ve stehu. Bhem dne se vydvaj na lov a vyhledvn potravy a v noci se ukrvaj v norch, kter si samy vyhrabvaj. Jsou tak znm svm chovnm, kdy se postav na zadn nohy a rozhlej se po okol. Surikaty jsou oblbenmi zvaty a jsou asto spojovny s ptelskm a hravm chovnm.",
