        id:27,
        name: "Tygr indick",
        max_age: "10-15 let",
        description_short: "Tygr indick je velk kokovit elma s charakteristickmi pruhy na srsti.",
        description_long: "Tygr indick (Panthera tigris tigris) je jednm ze esti ijcch poddruh tygra a je povaovn za nrodnho tvora Indie. Je to velk elma, kter obv pedevm lesy a ninn oblasti indickho subkontinentu, vetn Indie, Neplu, Banglade a Bhtnu. Tygr indick je znm svou velkou velikost a krsnou srst. Samec me doshnout dlky tla a 3 metry a hmotnosti pes 200 kg, zatmco samice jsou obvykle men. Maj oranovou a lutohndou srst s tmavmi pruhy, kter jsou charakteristick pro tygry. Tato srst slou jako kamufl v prosted lesa a pomh jim pi lovu.  Tygi indit jsou masoravci a lov rzn druhy koisti, vetn jelen, divokch prasat, opic, ptk a dalch mench zvat. Jsou velmi obratn a maj skvl loveck schopnosti. Lov pevn v noci, vyuvaj svj vynikajc zrak a sluch. Tygi indit jsou teritoriln a samott tvorov. Samci maj vt zem, kter si zna pomoc moi a drp, aby si udreli sv zem a odrazili ostatn tygry. Samice maj men zem, kter se me pekrvat s zemm vce samc. Tygi indit jsou ohroenm druhem a jejich populace je vrazn snen kvli ztrt pirozenho prosted, neleglnmu lovstv a konfliktm s lidskou populac. Organizace zamen na ochranu prody a vldy pijmaj opaten k jejich ochran, vetn vytven chrnnch oblast, snahy o zven povdom veejnosti o dleitosti ochrany tygr a potlaovn neleglnho obchodu s jejich stmi. Tygi indit jsou symbolem divokho a silnho prodnho ddictv a jsou dleit pro ekosystmy, ve kterch ij. Jejich ochrana a zachovn jsou klov pro udren biologick rozmanitosti a ekologick rovnovhy.",
