        description_short: "Velbloud dvouhrbn je velk savec se dvma hrboly na zdech a dlouhm chodidlem.",
        description_long: "Velbloud dvouhrb, tak znm jako velbloud arabsk (Camelus bactrianus), je velk suchozemsk savec, kter je znm svou schopnost pet v extrmn suchch a nehostinnch podmnkch pout a step. Je to jeden ze dvou hlavnch druh velbloud, piem druhm je velbloud jednohrb. Velbloudi dvouhrb maj charakteristick dv hrboly na zdech, kter slou jako zsobrna tuku, nikoli vody. Tento tukov hrb jim umouje pet dlouh obdob bez potravy a vody. Jsou pizpsobeni prochzce v psench dunch a maj irok a poltovit kopyto, kter jim pomh udrovat stabilitu na psitm povrchu. Velbloudi dvouhrb maj dlouh a hust chlup, kter jim pomh chrnit se ped extrmnmi teplotami. Bhem horkch dn maj schopnost ovldat sv tlesn teploty, co jim umouje udret se chladnmi. Jejich srst je obvykle hnd nebo ed, s nktermi jedinci majcmi svtlej odstny. Tito velbloudi jsou bloravci a jejich strava se skld pevn z trvy, list, vhonk a jinch rostlinnch materil, kter nachzej v poutnch oblastech. Jsou schopni se krmit i suchmi a tko stravitelnmi rostlinami, co je dal adaptace na ivot v drsnm prosted. Velbloudi dvouhrb jsou spoleensk zvata a ij ve stdech, kter se skldaj z nkolika samic, samc a jejich potomk. Jsou dobe pizpsobeni ivotu v pouti a jsou schopni putovat velk vzdlenosti v hledn potravy a vody. Maj vynikajc orientan schopnosti a pam, co jim pomh najt cestu zpt k vodnm zdrojm. Velbloudi dvouhrb maj historicky slouili jako cenn pomocnci pro lidi ijc v poutnch oblastech. Byli vyuvni jako tan zvata pro pepravu zbo a jako prostedek k peit v tkch podmnkch pout.",
