        max_age: "10-15 let",
        description_short: "Tygr usurijsk je velk kokovit elma s krsnm zlatohndm zbarvenm a ernmi pruhy.",
        description_long: "Tygr usurijsk, znm tak jako tygr amursk (Panthera tigris altaica), je jeden z poddruh tygra a pat mezi nejvt kokovit elmy na svt. Je pojmenovn po regionu Usuri v ruskm Dlnm vchod, kde se pirozen vyskytuj.  Tygr usurijsk je charakteristick svou mohutnou postavou a hustou srst, kter je zimnm ochlupenm del a silnj. Jeho srst m svtlej odstny a asto m vy podl bl barvy ve srovnn s jinmi poddruhy tygra. Dospl jedinci mohou dosahovat dlky tla pes 3 metry a hmotnosti a kolem 300 kg, co z nich in jedny z nejtch kokovitch elem. Tygr usurijsk obv pevn lesy, kopce a horsk oblasti vchodn Asie, zejmna rusk Dln vchod. Je pizpsoben chladnjm podnebnm podmnkm a extrmnm teplotnm rozdlm v tto oblasti. Svou velikost a loveckmi schopnostmi je na vrcholu potravnho etzce. Tygr usurijsk je masorav a jeho dieta se skld z rznch druh koisti, jako jsou jeleni, divok prasata, zajci a dal men zvata. Jejich lov je zaloen na skrytm piblen se ke koisti a nslednm rychlm toku. Tygr usurijsk je znm svou slou a obratnost pi lovu. Tento poddruh tygra je ohroen a el mnoha hrozbm. Ztrta pirozenho prosted v dsledku kcen les a roziovn lidskch osad je jednou z hlavnch pin poklesu jejich populace. Nelegln lov pro koeinu a obchod s stmi tla tygr pedstavuje tak znan nebezpe. Snahy o jejich ochranu zahrnuj vytven chrnnch oblast, vzdlvn veejnosti a prvn ochranu. Tygr usurijsk hraje dleitou roli v ekosystmech, kde ije. Pomh udrovat rovnovhu v populacch koisti a ovlivuje strukturu les svm ivotnm stylem.",
