        description_short: "Zebra je stedn velk savec s erno-blm pruhovanm zbarvenm.",
        description_long: "Zebra je ikonick africk zve, kter je znm svm charakteristickm pruhovanm vzorem na srsti. Pat do eledi kon (Equidae) a je pbuzn s komi a osly. Existuje nkolik druh zeber, vetn stepn zebry, Grevyho zebry a horsk zebr.  Zebrm dominuje ernobl vzor na srsti, kter je jedinen pro kadho jedince. Tyto pruhy slou jako ochrana ped predtory, protoe vytvej vizuln klam, kter jim pomh se v prodnm prosted snadno maskovat a ztuje uren jejich pesnho tvaru a pohybu. Zebrm jsou tak charakteristick jejich dlouh a thl nohy, kter jim umouj bh na vysok rychlosti. Jsou to rychl bci a dok se pohybovat a rychlost kolem 60 km/h. Jejich nohy jsou tak siln a vytrval, co jim umouje pekonvat dlouh vzdlenosti pi migraci za potravou a vodou.  Zebry jsou bloravci a jejich strava se skld pevn z trvy a list. Jsou adaptovny na ivot v savanch a pastvinch, kde nachzej dostatek potravy. Jejich zavac systm je tak pizpsoben na trven vlkniny obsaen v rostlinn strav.  Zebrm ij ve stdech, kter se skldaj z nkolika samic, mlat a nkolika samc. Tyto stda maj asto hierarchickou strukturu, kde vedouc samice zajiuje ochranu a organizaci skupiny. Samci bojuj o prvo se pit se samicemi pomoc svch silnch a ostrch kopyt. I kdy jsou zebry obecn povaovny za druhy s nim rizikem ohroen, nkter druhy, jako je Grevyho zebra, jsou ohroeny vyhubenm. Ztrta pirozenho prosted, nelegln lov a konflikty s lidskmi aktivitami jsou hlavnmi hrozbami pro jejich peit. Snahy o ochranu zahrnuj vytven chrnnch oblast, vzdlvn veejnosti a ochranu prosted, ve kterm ij.",
