        description_short: "irafa je vysok a thl savec s dlouhm krkem a skvrnitm zbarvenm.",
        description_long: "irafa (Giraffa camelopardalis) je majesttn a uniktn tvor obvajc africk savany. Je to nejvy suchozemsk savec na svt, piem samci mohou doshnout vky a 5,5 metru a hmotnosti pes 1 200 kg. irafa je znm svou dlouhou krkem, dlouhma nohama a charakteristickm vzorem na srst. Jej krk je pizpsoben k dosahovn potravy z vysokch strom. M dlouh a siln krn obratle a zvltn krevn obh, kter umouje efektivn transport krve do mozku i pi zdvihnutm krku. Jej nohy jsou tak extrmn dlouh a siln, co j umouje rychl a elegantn pohyb pes savanu. irafy maj npadn vzor na srsti, kter je tvoen tmavmi skvrnami na svtlejm podkladu. Kad irafa m uniktn vzor, co z n in snadno rozpoznateln jedince. Jejich srst je obvykle oranov nebo lutohnd, co jim pomh se skrt v prosted vysok trvy a ke. Tato majesttn zvata jsou bloravci a jejich strava se skld pevn z list, vhonk a pupen strom. Dky svmu dlouhmu jazyku a silnm elistem jsou schopny doshnout na potravu ve vce, kam ostatn zvata nedoshnou. Navzdory jejich vce maj irafy pouze sedm krnch obratl, co je stejn poet jako u vtiny savc. irafy jsou spoleensk zvata a vytvej stda, kter se skldaj obvykle z nkolika samic, mlat a samc. Samci asto vedou boje o prvo se pit se samicemi pomoc svch krk a hlav. Tyto souboje jsou znm jako giraf boje a jsou impozantnm zjevem. I kdy byly irafy dlouhou dobu povaovny za neohroen druh, jejich populace se v poslednch desetiletch vrazn snila kvli ztrt pirozenho prosted, neleglnmu lovu a konfliktm.",
