        max_age: "40-50 let",
        description_short: "Hroch je velk a tk savec s tlustou k, velkmi sty a irokmi nozdrami.",
        description_long: "Hroch je jednm z nejvtch suchozemskch savec na svt a pat do eledi hrochovitch (Hippopotamidae). Je znm svou obrovskou tlesnou hmotnost, masivnm tlem, tlustou k a velkmi sty s ostrmi tesky. Hroch je charakteristick svm poloakvatickm zpsobem ivota, trv vtinu svho asu ve vod a vyhledv pevn vodn prosted, jako jsou eky, jezera a bainy. Hroch m velkou hlavu s velkmi oima a uima, kter mu pomhaj pi pohybu ve vod. Jejich tlo je zakoneno robustnmi nohama s velkmi kopyty, kter jsou adaptovny na pohyb po bahnu a mkkm povrchu. Hmotnost hroch se pohybuje kolem 1,5 a 3,5 tuny, co z nich in jedny z nejtch suchozemskch ivoich. Hroi jsou bloravci a jejich strava se skld pevn z trvy a dal vegetace, kterou vyhledvaj na bezch vodnch tok. Jsou schopni se ponoit do vody a zstat pod vodou po del dobu, co jim umouje se chrnit ped plinm slunenm zenm a udrovat si tlesnou teplotu. Hroi jsou tak siln a agresivn obrnci svho teritoria a dok se brnit proti predtorm. Hrochov jsou spoleensk zvata a ij ve skupinch nazvanch podrky. Podrka se skld z nkolika samic, mlat a nkolika dominantnch samc. V obdob rozmnoovn dochz k soubojm mezi samci o prvo se pit se samicemi. Hrochov maj tak vrazn teritoriln instinkt a brn si sv zem ped ostatnmi sameky. I kdy jsou hroi povaovni za zraniteln druh, jejich populace se sniuje kvli ztrt pirozenho prosted, neleglnmu lovu a konfliktm s lidskou innost. Ztrta pstupu k vodnm zdrojm a nien jejich pirozench stanovi jsou hlavnmi hrozbou.",
