        description_long: "Krokodl je velk a thl plaz, kter je lenem eledi krokodlovitch (Crocodylidae). Jsou to vodn ivoichov se silnm tlem, dlouhm ocasem, krtkmi nohama a dlouhm enichem. Krokodli jsou znm svou hranatou hlavou, silnmi elistmi a velkmi zuby, kter pouvaj k lovu a zachycovn sv koist. Krokodli jsou uzpsobeni k ivotu ve vod. Maj siln nohy a plovac blny mezi prsty, co jim umouje se pohybovat rychle a elegantn ve vod. Jsou vbornmi plavci a jejich tlo je hydrodynamicky navreno pro snadn plavn. Na soui se pohybuj pomalu a neohraban. Krokodli jsou masoravci a jejich potrava se skld pevn z ryb, ptk, savc a dalch vodnch ivoich. Jsou to lovci, kte haj na svou koist a nhle vysko z vody, aby ji chytli a pithli pod hladinu. Krokodli maj siln elisti, kter dokou sevt s velkou silou, a jsou schopni roztrhat maso. Krokodli jsou teritoriln a brn si sv zem. Samec krokodla asto vykazuje agresivn chovn vi ostatnm samcm v dob pen. Samice krokodla kladou vejce v psku na behu vody a peliv je hldaj a do vylhnut mlat. Krokodli jsou peiv z prehistorick ry a jsou povaovni za evolun starobyl ivoichy. I kdy jsou krokodli obecn dobe pizpsobeni svmu prosted, jsou ohroeni nkolika faktory, vetn ztrty pirozenho prosted, lovu pro ki a maso a konflikt s lidmi. Snahy o ochranu krokodl zahrnuj zizovn chrnnch oblast, omezen lovu a vzdlvn veejnosti o jejich dleit roli v ekosystmu.",
