        description_short: "Aligtor je stedn velk plaz s upinatou k a charakteristickmi elistmi.",
        description_long: "Aligtor je druh plazi patc do eledi aligtorovitch (Alligatoridae). Jsou to podobn krokodlm, ale maj nkolik rozdl v anatomii, chovn a prosted, ve kterm ij. Aligtoi jsou obyvatel sladkovodnch prosted v jinch stech Spojench stt a nkterch stech Stedn a Jin Ameriky.  Aligtoi maj krtkou a irokou hlavu, kter je typick pro tento druh. Jsou men ne krokodli, piem samci dosahuj dlky okolo 3 a 4 metr, zatmco samice jsou ponkud men. Maj silnou tlesnou stavbu, krtk nohy a dobe vyvinut ocas, kter jim pomh pi plavn. Podobn jako krokodli, i aligtoi jsou vodn ivoichov a jejich tlo je uzpsobeno k ivotu ve vod. Maj siln nohy a plovac blny mezi prsty, co jim umouje se pohybovat ve vod a rychle plavat. Jsou skvl lovci a jejich potrava se skld z ryb, obojivelnk, ptk a dalch vodnch tvor. Aligtoi jsou znm svmi silnmi elistmi, kter jim umouj sevt svou koist s velkou silou. Jsou trpliv lovci, kte haj na svou koist a nhle ji zachyt, kdy je dostaten blzko. Jsou tak vborn plavci a mohou se ponoit pod vodu na del dobu. Aligtoi jsou teritoriln a brn si sv zem. Maj tendenci bt agresivn, zejmna v obdob rozmnoovn, kdy samec hj sv teritorium a bojuje o samici. Samice kladou vejce do hnzd vykopanch na behu vody a peliv je ste a do vylhnut mlat. I kdy aligtoi nejsou tolik ohroeni jako nkter druhy krokodl, stle el uritm hrozbm. Ztrta pirozenho prosted, lostvo, nelegln obchod s jejich k a naruovn jejich ivotnho prosted jsou faktory, kter mohou negativn ovliv",
