        description_long: "Antilopy jsou elegantn a rychl blorav savce, kter pat do eledi antilopovitch (Antilopidae). Jsou rozeny pevn v africkch savanch a stepch, ale nkter druhy se vyskytuj tak v Asii a na Arabskm poloostrov. Antilopy jsou znm svou gracizn postavou, dlouhmi nohami a rohy u nkterch druh. Existuje velk mnostv druh antilop, zahrnujcch rzn velikosti, tvary roh a barvy srsti. Mezi nejznmj druhy pat gazely, impaly, elandy, kudu, antilopa kosk a antilopa skkav. Kad druh m sv charakteristick vlastnosti a pizpsoben prosted, ve kterm ije.  Antilopy maj thl tlo, kter je ideln pro rychl bh a skkn. Maj siln nohy a dlouh thl nohy, kter jim umouj pekonvat velk vzdlenosti a vyhledvat potravu. Jsou to vynikajc bci a mohou doshnout vysokch rychlost, m se brn predtorm a spn se pizpsobuj prosted, ve kterm ij. Antilopy jsou pizpsoben ke konzumaci rostlinn stravy. Jejich strava se skld pevn z trvy, list, vhonk a kvt. Jsou schopny najt si potravu i v suchch oblastech, kde je omezen pstup k vod. Nkter druhy antilop dokonce vyvinuly schopnost pevat dlouhou dobu bez vody, zskvaj ji z rostlinn stravy.  Samec antilopy je obvykle vt ne samice a asto m npadn rohy. Tyto rohy slou k obran, souten o samice a teritorilnm bojm s jinmi samci. U nkterch druh antilop jsou rohy velmi dlouh a maj sloitou strukturu. U jinch druh jsou rohy krat a mn vrazn. Antilopy maj vborn zrak a sluch, co jim pomh vas zjistit ptomnost predtor a uniknout jim. Jsou dobe pizpsoben ivotu v otevench oblastech, kde maj ir vhled.",
