        name: "Vlk",
        max_age: "10-15 let",
        description_short: "Vlk je stedn velk savec ze skupiny ps s hustou srst a vraznm smekovm chovnm.",
        description_long: "Vlk je drav savec, kter pat do eledi psovitch (Canidae) a je pirozen rozen v rznch stech svta, vetn Severn Ameriky, Evropy, Asie a Severn Afriky. Vlk je znm svou sociln strukturou a schopnost t ve smekch. Vlci maj thl tlo a vraznou hlavu s tupm enichem. Maj siln nohy, kter jsou pizpsobeny rychlmu bhu a pronikavm drpm, kter jim umouj kopat a plhat. Maj dlouhou a huatou srst, kter je asto ed nebo hnd, ale existuj tak rzn barevn varianty.  Vlci jsou spoleensk zvata a ij ve smekch, kter se skldaj z nkolika jedinc. Smeka je veden alfa samcem a alfa samic, kte maj hlavn roli pi zen smeky. Vlci spolupracuj pi lovu a ochran zem. Jsou to vytrval bci a dok pronsledovat svou koist na dlouh vzdlenosti.  Vlci jsou masoravci a jejich potrava se skld pevn z mench savc, jako jsou jelena, sobi, zajci a drobn hlodavci. Jsou tak schopni lovit vt zvata ve smekch. Vlci jsou dleitm regulanm faktorem v ekosystmech, protoe kontroluj populaci sv koisti.  Vlci maj vyvinut smysly, vetn vynikajcho ichu a sluchu. Jsou tak velmi inteligentn a maj vyspl sociln chovn. Vytvej siln pouto ve smece a vzjemn se peuj o mlata. Vlci komunikuj rznmi zvuky, vetn vyt, tkotu a vren. Vlk je v mnoha kulturch symbolickm zvetem a asto je spojovn s duchovnm vznamem a slou. V minulosti byl vlk ohroen lovu a ztrt pirozenho prosted, ale dky ochranskm opatenm se nkter populace vzpamatovvaj. V souasn dob je vzvou udret rovnovhu mezi lidskmi zjmy a ochranou vlk.",
