        max_age: "10-15 let",
        description_short: "Anakonda je velk had elma se silnm tlem a schopnost se svjet kolem sv koisti.",
        description_long: "Anakonda je jednm z nejvtch had na svt a pat mezi tzv. sumcovit hady. Jej velikost a sla ji in impozantnm predtorem ve svm prosted. Anakondy se vyskytuj v tropickch detnch lesch a mokadech Jin Ameriky, zejmna v oblastech jako je Amazonie a Orinoko. Nejznmj druhy anakond jsou zelen anakonda (Eunectes murinus) a lut anakonda (Eunectes notaeus). Zelen anakonda je povaovna za nejvt had druh na svt a me dosahovat dlky pes 6 metr a hmotnosti pes 100 kg. Anakondy maj siln tlo, kulatou hlavu a velkou slu, kterou vyuvaj pi lovu a uchopen svch koist. Anakondy jsou vodn hadi a jsou dobe pizpsoben ivotu ve vod. Maj ploch hlavy a nosn otvory na vrcholu hlavy, co jim umouje zstat tm zcela ponoen a pitom dchat. Jsou vynikajc plavci a potpi a dok lovit ryby, obojivelnky, ptky a dal ivoichy, kte ij v jejich vodnm prosted. Anakondy jsou nenpadn a dobe maskovan ve svm pirozenm prosted. Maj tmavou barvu s promnlivmi skvrnami, kter jim pomhaj se skrt mezi vodn vegetac a bahnem. Jsou aktivn hlavn v noci a za soumraku, kdy vyuvaj svj zrak a teploivn jamky na hledn koisti. Samec anakondy je obvykle men ne samice a maj vraznj thl tlo. Samec se me zapojit do ritul pen, kter zahrnuj ten hlav a vzjemn svinovn tl. Samice rod iv mlata, kter maj ji po narozen pln vyvinutou schopnost plavat a lovit. Anakondy jsou velmi dleit pro ekosystmy, ve kterch ij, a pispvaj k udrovn rovnovhy ve vodnch a pilehlch ekosystmech. Nicmn, jako mnoho jinch druh, i anakondy el hrozbm, jako je ztrta pirozenho prosted.",
