        name: "Hrozn",
        max_age: "20-30 let",
        description_short: "Hrozn je velk had se silnm tlem a vraznm vzorem na ki.",
        description_long: "Hrozn krlovsk (Boa constrictor) sice nen klasickm domcm zvetem, ale rozhodn pat k tm nejpsobivjm. Jak obtn je vlastn chov v terriu? V naem portrtu zskte nejdleitj informace o pi o obho hada. Vysvtlme Vm tak, pro je v podstat pomrn nenronm obyvatelem terri. Hrozn krlovsk pat do druhov bohat eledi hroznovit (Boidae). Hrozni jsou krtii, kte svou koist usmrcuj tak, e ji obto kolem tla. Oznauje to latinsk vraz constrictor, kter lze peloit jako stahova. Mezi hroznovit pat nkter z nejvtch druh had na svt, napklad anakonda. Pirozen arel hrozn se rozprostr od severnho Mexika pes Stedn Ameriku a po Jin Ameriku. Hroznovit najdeme v rznch biotopech. Obvaj vlhk detn pralesy, jako je amazonsk dungle, such savany a kovinat krajiny, ale tak tropick horsk lesy. Nejastji se hrozn krlovsk vyskytuje v blzkosti vodnch ploch.",
