        description_short: "Krajta mkovan je velk had s robustnm tlem a charakteristickm mkovanm vzorem na ki.",
        description_long: "Krajta mkovan, znm tak jako krajta indick, je jednm z nejvtch druh nejedovatch had na svt. Je to psobiv had, kter se vyskytuje v jihovchodn Asii, zejmna v Indii, Pkistnu, Bangladi, Neplu a Sr Lance. Krajta mkovan pat do eledi hroznovitch a je velmi cenna pro svou krsnou a vraznou kresbu na tle. Krajty mkovan jsou znm svm robustnm tlem, kter me doshnout dlky a pes 5 metr. Maj krtkou, silnou hlavu a masivn tlo pokryt velkmi upinami. Jejich zkladn barva me bt rzn, od hnd a po edou, a jsou zdobeny charakteristickmi mkami nebo pruhovanm vzorem ern, hnd nebo lut barvy. Tento vzor jim poskytuje vynikajc kamufl v jejich pirozenm prosted. Krajty mkovan jsou vborn lovci a jejich potrava se skld pevn z mench savc, ptk a jetrek. Jsou to konstriktorov, co znamen, e obtuj svou koist omotnm tla a stlaenm, co brn jejich dchn a vedou k smrti obti. Krajty mkovan jsou znm tak svmi silnmi elistmi a jsou schopny porat koist mnohem vt ne jejich hlava. Tento druh hada je vborn lezec a me se pohybovat jak po zemi, tak ve stromech. Jejich svalnat ocas jim poskytuje stabilitu pi plhn. Krajty mkovan jsou pevn non a plach hady, kte se bhem dne skrvaj v krytech, jako jsou dutiny strom, skaln trbiny nebo hust vegetace. Krajty mkovan jsou oblbenmi chovanci v terarich dky svmu zajmavmu vzhledu. Nicmn, kvli nadmrnmu lovu a ztrt pirozenho prosted jsou nkter populace ohroen. Mezinrodn obchod s krajtami mkovanmi je tak regulovn.",
