        name: "Krajta krlovsk",
        max_age: "20-25 let",
        description_short: "Krajta krlovsk je velk had s robustnm tlem a charakteristickm geometrickm vzorem na ki.",
        description_long: "Krajta krlovsk, tak znm jako krajta kvtinov, je jednm z nejznmjch a nejvce obdivovanch druh had na svt. Je to velk, nejedovat had, kter se vyskytuje pedevm v jin a jihovchodn Asii, vetn zem jako Indie, Myanmar, Thajsko, Indonsie a Malajsie. Krajta krlovsk pat mezi nejdel hady na svt a me doshnout dlky a pes 5 metr. Krajty krlovsk jsou znm svm ndhernm zbarvenm a vzorovnm na tle. Maj hladk upiny a zkladn barva jejich tla se pohybuje od svtlho edho po olivov hnd odstn. Na jejich tle se nachz charakteristick vzory ve form hndch nebo ernch pruh, skvrn a o, kter se mohou podobat kvtm nebo listm. Tyto vzory jim pomhaj pi kamufli a skrvn se v prodnm prosted.  Krajty krlovsk jsou aktivn ve dne i v noci, akoli vtinou preferuj non ivotn styl. Jsou vynikajc lovci a jejich potrava se skld pevn z mench savc, ptk a jetrek. Jsou to konstriktorov a pi lovu se omotvaj kolem sv koisti a stiskem ji dus. Pot svou koist polykaj celou. Krajty krlovsk maj schopnost rozthnout sv elisti, co jim umouje spolknout koist mnohem vt ne je jejich vlastn hlava.  Tito hadi maj vborn ich a jejich jazyk slou k vnmn pach ve svm prosted. Maj vborn zrak a smysl pro dotek, kter jim pomhaj najt a identifikovat koist. Krajty krlovsk jsou asto nalezeny v blzkosti vodnch tok nebo v hust vegetaci, kde si mohou vytvoit sv kryty. Krajty krlovsk maj vznamnou roli v mstnch kulturch a nboenskch tradicch. V nkterch oblastech jsou povaovny za posvtn hady a jsou respektovny a chrnny.",
