        max_age: "15-20 let",
        description_short: "Chest skaln je stedn velk jedovat had s charakteristickm hranatm tvarem hlavy.",
        description_long: "Chest skaln, znm tak jako chest zpadn, je jednm z nejznmjch jedovatch had na svt. Pat do eledi chestovitch a vyskytuje se pedevm v Severn Americe, zejmna v oblastech jako jsou Skalnat hory a pout zpadnho pobe Spojench stt a Kanady. Chest skaln je znm svou charakteristickou trojhelnkovou hlavou a chestidlem, kter se nachz na konci jeho ocasu. Chestidlo je tvoeno zrohovatlmi lnky, kter pi pohybu hada vydvaj zvuk podobn rachotu. Tento zvuk slou jako varovn pro ostatn tvory v okol a upozoruje je na ptomnost jedovatho hada. Chesti skaln maj rzn zbarven, kter se pohybuje od edohnd a po olivov zelenou barvu. Jsou dobe pizpsobeni svmu prosted a maj schopnost se skvle maskovat v okoln krajin. Maj siln tlo a krtk nohy, kter jim umouj se pohybovat na skalnatm ternu. Jed cheste skalnho je siln a pat mezi nejinnj had jedy. Jejich jed obsahuje neurotoxiny, kter napadaj nervovou soustavu koisti. Pokousn chestem me bt pro lovka nebezpen a vyaduje okamitou lkaskou pomoc. Chesti skaln jsou pevn non hadi, kte bhem dne asto odpovaj v krytech, jako jsou skaln trbiny nebo podzemn nory. Jsou to samott tvorov a svj teritorium brn proti ostatnm hadm. Potravu tvo pevn men savci, ptci a plazi. Tito hadi maj dleitou roli v ekosystmech, ve kterch ij. Pomhaj regulovat populaci hlodavc a jsou potenciln koist pro predtory vyho du. Nicmn, jejich populace je ohroena ztrtou pirozenho prosted a neleglnm lovectvm. Je dleit dodrovat opatrnost a respektovat ptomnost chest skalnch ve voln prod.",
