        description_long: "Kobra krlovsk, znm tak jako indick kobra, je jednm z nejvce ikonickch had na svt. Je to jedovat had, kter se vyskytuje pedevm v Indii, Pkistnu, Sr Lance a dalch oblastech jin a jihovchodn Asie. Kobra krlovsk je znm svou charakteristickou schopnost vztyit svou krlovskou kapuci, kdy se ct ohroen. Kobra krlovsk m pomrn robustn tlo a dosahuje dlky obvykle mezi 1,5 a 2,5 metry, piem samci bvaj obvykle vt ne samice. Jej zbarven se pohybuje od olivov zelen a po hndou, a asto je doplnno tmavm vzorem na hbet. Kdy se ct ohroena, kobra krlovsk doke rozit svou krlovskou kapuci, co je vrazn plocha ke na krku, aby vypadala vt a odstraovala potenciln predtory nebo neptele. Kobra krlovsk je znm svm jedovm kousnutm. Jej jed obsahuje neurotoxiny, kter ovlivuj nervov systm obti. Jej kousnut me bt pro lovka smrteln nebezpen, a proto je dleit vyvarovat se kontaktu s touto jedovatou hadic a pijmout okamitou lkaskou pomoc v ppad kousnut.  Kobra krlovsk se iv pevn malmi obratlovci, jako jsou hlodavci, jetrky, ptci a dokonce i jin hadi. Je schopna koist sledovat a nsledovat pomoc svho zraku a ichu. Jej hlavn strategi pi lovu je paralyzovat koist svm jedem a pot ji pozt.  V nkterch kulturch, zejmna v Indii, je kobra krlovsk vznamn a m nboensk a symbolick vznam. Je spojovna s bohy a bv zobrazovna na mnoha tradinch a nboenskch artefaktech. Kobra krlovsk je chrnnm druhem v mnoha zemch a je zakzno ji lovit nebo obchodovat s n. Ochrana prody a zachovn pirozenho prosted, ve kterm kobra krlovsk ije.",
