        description_long: "Kobra indick, znm tak jako indick kobra, je jednm z nejznmjch druh had na svt. Je endemick v Indii a nkterch okolnch zemch jin Asie. Tato jedovat hadice je znm svm vraznm vzhledem, kter zahrnuje vztyen kapuce a charakteristick vzor na krku. Kobra indick je stedn velk had, dosahuje dlky obvykle mezi 1,5 a 2 metry, piem samci mohou bt o nco vt ne samice. Jej tlo je thl a pohybliv. M hladkou ki s charakteristickm vzorem, kter se skld z tmavch pruh nebo skvrn na svtlejm podkladu. Tento vzor se me mezi jednotlivmi jedinci liit. Jed kober indickch je neurotoxick a obsahuje sloky, kter ovlivuj nervov systm. Jejich kousnut me bt pro lovka smrteln nebezpen. Pokud dojde ke kousnut kobrou indickou, je nezbytn okamit vyhledat lkaskou pomoc a podstoupit antivenomovou lbu. Kobra indick se iv pevn malmi obratlovci, jako jsou hlodavci, jetrky a ptci. Jej zpsob lovu je zaloen na skrytm pekvapen. Hadi tvo svou potravu vhradn z masa a maj vborn zrak a ich, co jim pomh lokalizovat koist. Tito hadi jsou znm svm agresivnm chovnm, zejmna pokud se ct ohroeni. Pi pocitu nebezpe dokou vztyit svou kapuci, co je charakteristick chovn, kter slou jako varovn pro predtory a neptele. V mnoha kulturch m kobra indick symbolick a nboensk vznam. Je spojovna s hinduistickou bohyn Mri, kter je asto zobrazovna s kp kobry na svm krku. V nkterch oblastech jsou tyto hady uctvny a chrnny. Je dleit respektovat a chrnit tyto hady ve voln prod. Ochrana jejich pirozenho prosted a osvta veejnosti o jejich vznamu je klov pro udren.",
