        description_short: "Maba ern je jedovat had s robustnm tlem a hladkou k.",
        description_long: "Maba ern, znm tak jako ern mamba, je jednm z nejnebezpenjch had na svt. Jedn se o velmi jedovatho hada, kter se vyskytuje v subsaharsk Africe. Jej jmno je odvozeno od charakteristickho ernho zbarven tla.  Maba ern pat mezi nejrychlej hady na svt. Dosahuje dlky mezi 2,5 a 4 metry, piem samice bvaj obvykle vt ne samci. Jej tlo je thl, elegantn a hladk. Maba ern m pevnou ernou barvu po celm tle, kter j poskytuje skvlou kamufl v hustch lesch a travnatch oblastech, ve kterch ije.  Jed maby ern obsahuje neurotoxiny a kardiotoxiny, kter ovlivuj nervov a srden systm obti. Jej kousnut je velmi nebezpen a me vst k rychle se rozvjejcm pznakm otravy, vetn paralzy a zstavy dechu. Je nezbytn okamit vyhledat lkaskou pomoc a podstoupit antivenomovou lbu, aby se minimalizovaly nsledky kousnut. Maba ern je aktivnm predtorem a iv se pevn malmi savci, ptky a plazy. Jej loveck strategie spov ve skrytm pekvapen, rychlm toku a seven koisti. Maba ern je znm svmi silnmi svaly a silnou elist, kter j umouj efektivn se vypodat s jejmi koist. Tento had se obvykle vyhb kontaktu s lidmi a preferuje izolovan msta, kde se me skrvat ped potencilnmi predtory. Pestoe je maba ern povaovna za nebezpenho, obecn se vyhb agresi, pokud nen ctit ohroen. Je dleit respektovat mabu ernou a chrnit jej pirozen prosted. Snahy o zachovn jejho pirozenho habitatu a edukace veejnosti o bezpenosti a prevenci kousnut hady jsou klov pro minimalizaci incident a zachovn tto dleit sousti biodiverzity subsaharsk Afriky.",
