        description_long: "Kobra ervenobich, tak znm jako kobra krlovsk ervenobich (Naja siamensis), je jednm z druh had z eledi korlovcovitch (Elapidae). Tento had se vyskytuje v jihovchodn Asii, zejmna v Thajsku, Laosu, Kambodi a Vietnamu.  Kobra ervenobich je stedn velk had s dlkou obvykle mezi 1,2 a 1,8 metry. Jej tlo je pomrn robustn, a jej barva me bt zeleno-hnd a olivov. Charakteristickm rysem tohoto druhu je erven zbarven bicho, kter slou jako varovn pro predtory. Jed kober ervenobichch je neurotoxick a obsahuje sloky, kter ovlivuj nervov systm obti. Jejich kousnut me bt pro lovka velmi nebezpen a vyaduje okamitou lkaskou pomoc. Antivenomov sra jsou dostupn pro lbu kousnut hady ttoho druhu.  Tyto hady se iv pevn menmi savci, plazy a ptky. Jsou aktivn v noci a lov svou koist pomoc skrytho pekvapen. Kobra ervenobich m schopnost zvedat tlo a vytvet charakteristick tvar S, co je varovn pro potenciln predtory. Je dleit respektovat a chrnit tyto hady ve voln prod. Ochrana jejich pirozenho prosted, prevence kousnut hady a vzdlvn veejnosti o jejich chovn a bezpenosti jsou klov pro minimalizaci konflikt mezi lidmi a tmto druhem hada.",
