        description_long: "Kobra vchodn, tak znm jako kobra krlovsk vchodn (Ophiophagus hannah), je velk a jedovat had, kter se vyskytuje v jihovchodn Asii. Je to nejdel jedovat had na svt, kter me dosahovat dlky a pes 5 metr. Kobra vchodn je znm svm charakteristickm vzhledem. M dlouh, zk tlo, kter je obvykle pokryt olivov zelenou barvou s tmavmi pruhy. Jej hlava je relativn mal s vraznmi elistmi a vkonnmi jedovmi lzami. Na krku m charakteristick rozen, kter j umouje zvedat a roztahovat krk pi obran. Jed kober vchodnch je siln neurotoxin, kter me bt pro lovka smrteln. Jejich kousnut vyvolv rychl pznaky otravy, kter zahrnuj slabost, zvrat, svalovou paralzu a selhn dchn. V ppad kousnut je nezbytn okamit vyhledat lkaskou pomoc a podstoupit antivenomovou lbu. Tito hadi se iv pevn dalmi hady, plazy a malmi savci. Jsou dobe pizpsobeni lovu a maj vynikajc smysl pro zjiovn koisti. Kobra vchodn je aktivn ve dne i v noci a obvykle se pohybuje rychle a tie, aby pekvapila svou koist. Vzhledem k vysok toxicit jedu a agresivn obran je dleit udrovat odstup od tchto had a nedotkat se jich v prod. Ochrana jejich pirozenho prosted, vzdlvn veejnosti o bezpenosti a prevenci kousnut hady jsou dleit pro zachovn tohoto druhu a minimalizaci konflikt mezi lidmi a hady.",
