        description_long: "Zmije rohat, znm tak jako krajta rohat (Cerastes cerastes), je jednm z druh jedovatch had z eledi zmijovitch. Tento had se vyskytuje v poutnch a polopoutnch oblastech severn Afriky a jihozpadn Asii. Zmije rohat je pomrn mal had s dlkou obvykle mezi 30 a 60 cm. Jej tlo je robustn a zbarven se me liit podle prodnho prosted, ve kterm se vyskytuje. Jej charakteristickm rysem jsou rohy na hlav, kter slou jako kamufln adaptace, pomhajc j se snadnji zamaskovat v psitm prosted. Jed zmije rohat obsahuje neurotoxiny, kter ovlivuj nervov systm obti. Jej kousnut je bolestiv a me zpsobit otoky, silnou bolest, krvcen a dal systmov pznaky otravy. V ppad kousnut je dleit okamit vyhledat lkaskou pomoc a podstoupit antivenomovou lbu. Tito hadi jsou non a iv se pevn menmi obratlovci, jako jsou hlodavci, jetrky a dal hadi. Jsou pomal a vtinu asu trv zakopni v psku, ekajc na koist, kter se pibl dostaten blzko. Pot se rychle vyno a ude svou jedovatou elist. Zmije rohat je pirozen plach a obvykle se vyhb kontaktu s lidmi. Jej agresivita je vyvolna pocitem ohroen. Je dleit dodrovat opatrnost a respektovat tuto had druhinu, pokud se vyskytuje v prodnm prosted, a vyvarovat se situacm, kter by mohly vst ke konfliktu. Zachovn prodnch prosted, kde se zmije rohat vyskytuj, a vzdlvn veejnosti o jejich chovn a bezpenosti jsou klov pro minimalizaci konflikt mezi lidmi a tmto druhem hada.",
