        description_long: "Gekon modr, tak znm jako Gekon metalick (Lygodactylus williamsi), je mal jetrka z eledi gekonovitch. Tento krsn gekon je endemickm druhem, kter se vyskytuje pouze v uritch oblastech horskho lesa na Nov Kaledonii, francouzskm zmoskm zem v Tichomo. Gekon modr je zvltn a vjimen svou vzhledem. Samci maj intenzivn azurov modr zbarven s ernmi prouky a tekami, zatmco samice jsou spe hnd a maj mn vrazn zbarven. Tlo tohoto gekona je prothl a lehce stlaen s dlouhm ocasem. Dosahuje dlky asi 10 cm. Tento druh je znm svm chovnm, kter zahrnuje spe denn aktivitu. Gekoni mod jsou velmi pohybliv a zrun, pi pohybu se dokou pidrovat i na vertiklnch plochch dky svm lepivm prstm. iv se pevn hmyzem, jako jsou cvrci, mouchy a pavouci. Gekoni mod jsou vejcorod a samice kladej sv vejce do hnzda ve vlhk pd. Po inkubaci se lhnou mal, pln vyvinut gekoni, kte jsou schopni samostatnho ivota. Bohuel, gekoni mod jsou ohroenm druhem kvli nien jejich pirozenho prosted a neleglnmu odchytu pro obchodovn na ernm trhu se zvaty. Mezinrodn mluva o obchodu s ohroenmi druhy voln ijcch ivoich a rostlin (CITES) zakazuje mezinrodn obchod s gekony modrmi, aby se chrnila jejich populace. Zchranu tohoto druhu je teba brt vn a podporovat ochranu jejich pirozenho prosted. Vzdlvn veejnosti o dleitosti ochrany biodiverzity a podpora udritelnho turismu jsou dleit pro zachovn gekona modrho a dalch ohroench druh na Nov Kaledonii.",
