        description_long: "Ara, znm tak jako ara velk, je peste zbarven ptk patc do eledi papoukovitch. Tento impozantn ptk je znm svmi vraznmi barvami a dlouhm ocasem, kter mu dodv majesttn vzhled.  Ara je domorodm obyvatelem tropickch detnch les Stedn a Jin Ameriky. Meme je nalzt v rznch stech kontinentu, vetn Amazonie, Pantanalu a Andskch hor. Ara je aktivnm a spoleenskm ptkem, kter ije ve velkch hejnech a komunikuje hlasitm volnm. Co je nejvce npadn na arch, je jejich pestrobarevn opeen. Maj vrazn zbarven pe, kter zahrnuje kombinaci erven, modr, lut, zelen a oranov barvy. Kad jedinec m svj uniktn vzor a barevn rozvren, co je pinou jejich ohromnho estetickho pvabu. Ara je velmi inteligentn a schopn ptk. Je znmo, e ara doke opakovat a imitovat lidskou e, co je dvod, pro jsou oblbenmi domcmi mazlky a atrakc v zoologickch zahradch. Jejich inteligence a sociln povaha tak umouj vytven silnch pout s lidmi. Tento druh papouka je blorav a iv se rznmi plody, semeny, oechy a kvty. Ara je tak schopn vyhledvat minerly v pd a dokonce konzumovat jlov bidlice, co jim pomh vyrovnvat stravitelnost potravy. Bohuel, ara je ohroen druh. Jejich populace se sniuje kvli ztrt pirozenho prosted v dsledku kcen les a neleglnmu odchytu pro obchod s exotickmi ptky. Mezinrodn ochrana a sil o udriteln lesnictv jsou nezbytn pro zachovn tohoto krsnho ptka. Ara je ikonickm ptkem, kter ns fascinuje svou krsou a inteligenc. Je dleit si vit a chrnit tyto vzcn tvory, aby mohly pokraovat ve svm prodnm prosted a pinet radost a as vem.",
