        description_long: "Baant zlat, vdecky nazvan Chrysolophus pictus, je jednm z nejkrsnjch a nejvce rozpoznatelnch druh baant na svt. Tento ndhern ptk je znm svmi vraznmi zlatmi a oranovmi pem a dlouhm ocasem, kter se vyznauje pestrou kresbou a dlkou. Baant zlat je pvodem z oblasti vchodn Asie, pedevm z ny. Jeho impozantn vzhled se stv pedmtem obdivu a zjmu mnoha lid po celm svt. Dky svm ndhernm barvm se baant zlat stal oblbenm zvetem v zoologickch zahradch a je tak asto chovn jako okrasn ptk. Samec baanta zlatho je znm svm vraznm zbarvenm. Jeho hva je oranov a rud, zatmco hrudnk a kdla maj zlat odstn. Jeho ocas je dlouh a nese pestrou kresbu s pruhovanmi a tekovanmi vzory. Samice jsou mn npadn, s hndm pem, kter jim pomh lpe se maskovat v prodnm prosted. Baanti zlat jsou aktivn ptci a pevn zemn. Vtinu asu trv hlednm potravy na zemi, kde se iv semeny, plody, hmyzem a jinmi drobnmi bezobratlmi. Jsou to tak vborn letci a v ppad ohroen se dok rychle vznesenout do vzduchu a ulett. Tito baanti jsou tak dob rodie. Samec vykazuje agresivn chovn pi ochran sv teritoria a samice stav hnzdo na zemi. Do hnzda klade vajka, ze kterch se po tyech tdnech lhnou mlata. Rodie se o n staraj a pomhaj jim se zskvnm potravy a ochranou. Bohuel, baant zlat je ohroen druh v dsledku ztrty pirozenho prosted a lovu. Ztrta lesnch oblast a zmny v ivotnm prosted znamenaj sniovn jejich populace. Mezinrodn ochrana a sil o udriteln rozvoj jsou nezbytn pro zachovn tohoto vzcnho druhu.",
