        description_long: "Jeb ernokrk, vdecky znm jako Grus nigricollis, je velk ptk z eledi jebovitch. Je jednm z nejvtch druh jeb na svt a vynik svm vzhledem, vetn dlouhho krku a nohou, thl postavy a charakteristickho ernho zbarven na krku. Tento druh jeba se vyskytuje v nkterch oblastech Asie, vetn vchodnho Kazachstnu, severozpadn ny, Mongolska a Sibie. Jebi ernokrk obvaj pedevm mokady, bainy, jezera a eky. Jsou to tan ptci a na zimu migruj do teplejch oblast, jako je Indie, Jin Korea nebo Japonsko. Jebi ernokrk maj vrazn ern zbarven na krku, hru a hlav, co je tak pina jejich jmna. Zbytek jejich tla je pevn ed nebo edohnd. Dospl jedinci maj na hlav charakteristickou ervenou korunu, kter jim dodv jet vt atraktivitu. Tito jebi jsou spoleenskmi ptky a obvykle ij ve skupinch, kter se nazvaj hejna. Vytv si teritoria a vykonvaj rzn tanen a zvukov projevy jako soust svho pcho ritulu. Jebi ernokrk jsou monogamn a p se na cel ivot. Potrava jeb ernokrkch se skld pevn z rostlinnch materil, jako jsou koeny, hlzy, semena, trva a listy. Obas se iv tak hmyzem, mkki a drobnmi vodnmi ivoichy. I pestoe jebi ernokrk jsou povaovni za mlo doten druh, jejich populace je ohroena nktermi faktory, jako je ztrta pirozenho prosted, lovectv a naruovn jejich hnzdnch stanovi. Mezinrodn ochrana a sil o zachovn jejich pirozenho prosted jsou nezbytn pro dlouhodobou ochranu tohoto impozantnho ptka. Jebi ernokrk jsou fascinujc a elegantn tvory, kter pinej krsu do pody.",
        food: "Pevn se iv rostlinnou stravou, jako jsou trva, semena, bobule a vodn rostliny.",
