        id:59,
        name: "Krkavec blokrk",
        max_age: "15-20 let",
        description_short: "Krkavec blokrk je stedn velk ptk s vraznm zobkem, ernm pem a blm zbarvenm na krku.",
        description_long: "Krkavec blokrk, vdecky znm jako Corvus albicollis, je druh krkavce, kter je rozen v nkterch stech Afriky. Jeho jmno blokrk vychz z charakteristickho blho zbarven na krku. Tento druh krkavce je stedn velk ptk s robustnm tlem a dlouhm ocasem. M leskl ern pe, kter je na bie mrn ed. Jeho vraznm znakem je bl skvrna na krku, kter vytv kontrast s ernm zbarvenm tla. Krkavec blokrk obv rzn typy prosted, vetn savan, kovinatch oblast a polopout. Je znm svou schopnost adaptovat se na rzn prosted a je schopen pet v podmnkch s omezenou dostupnost vody. Tito ptci jsou spoleent a ij ve velkch hejnech. Jsou inteligentn a maj vynikajc schopnost uen. Jejich strava je pestrobarevn a zahrnuje irokou klu potravy. Krkavci blokrk se iv rznmi druhy hmyzu, hmyzoravmi savci, plazy, vejci, ovoce, semeny a mrinami. Krkavci blokrk jsou tak znm svmi schopnostmi vyuvat nstroje pi zskvn potravy. Napklad mohou pout vtviky nebo klacky k zskn potravy z zkch mezinkch.  Tento druh ptka se rozmnouje v hnzdnch kolonich, kde samice klade vejce do hnzd postavench na vtvch strom. Oba rodie se podlej na stavb hnzda, inkubaci vajec a pi o mlata.  Krkavec blokrk m dobe pizpsoben sociln struktury a komunikan schopnosti. Mezi leny hejna se vzjemn dorozumvaj pomoc rznch zvuk a pohyb. Tyto komunikan dovednosti jsou dleit pro koordinaci skupiny a varovn ped nebezpem. I pestoe krkavec blokrk nen povaovn za ohroen druh, ztrta pirozenho prosted, odlesovn a rozvoj lidskch sdel.",
