        id:62,
        name: "Orel krlovsk",
        max_age: "30-35 let",
        description_short: "Orel krlovsk je velk dravec s tmav hndm pem, blou hlavou a ocasem, a lutmi drpy a zobkem.",
        description_long: "Orel krlovsk, latinsky Aquila heliaca, je impozantn druh dravce, kter pat mezi nejvt orly na svt. Jeho majesttn vzhled a vzneen ptomnost jej ad mezi nejchvatnj ptky v prod. Orel krlovsk je znm svou vjimenou slou, eleganc a schopnost lov ve vzduchu. Dospl jedinci orla krlovskho maj rozpt kdel dosahujc piblin 200-240 cm a v kolem 4-7 kg. Jsou vybaveni mohutnmi hndmi kdly, ziv lutm zobkem a mohutnmi drpy, kter jsou innmi nstroji pro lov koisti. Jejich pe je tmavohnd s vraznm zlatm odstnem na hbet a svtlejm bichem. Orel krlovsk je rozen pedevm v Eurasii, v oblastech s otevenou krajinou, rozshlmi lesy a horskmi oblastmi. iv se pevn drobnmi a stedn velkmi savci, jako jsou zajci, prasata, srnci a jinou koist, kterou doke chytit a zabt svmi silnmi drpy. Je tak schopen se ivit mrinami a rybami. Rozmnoovn orla krlovskho je spojeno s pelivou p a nronm ritulem pen. Samice sn obvykle 1-2 vejce, kter inkubuje po dobu piblin 45 dn. Oba rodie se podlej na kladen a inkubaci vajec, stejn jako na pi o mlata po vylhnut. Mlad orlov opoutj hnzdo po nkolika mscch, ale zstvaj zvisl na rodich jet njak as, ne se osamostatn. Orel krlovsk je chrnn druh a je povaovn za ohroen kvli ztrt pirozenho prosted, lovm a otravm. Snahy o ochranu tohoto majesttnho ptka zahrnuj monitorovn populac, vytven rezervac a osvtu veejnosti o dleitosti ochrany dravc. Je fascinujc sledovat orla krlovskho v letu.",
