        description_long: "Orel skaln, latinsky Aquila chrysaetos, je jednm z nejvtch a nejimpozantnjch druh orl na svt. Pat do eledi jestbovitch a je znm svm majesttnm vzhledem a vynikajcmi loveckmi schopnostmi. Jeho rozpt kdel se pohybuje kolem 200-250 cm, co z nj in vraznho dravce. Orel skaln se vyskytuje ve skalnatch oblastech, vysokch horch a horskch psmech. Jeho pe je vtinou hnd, s tmavm zbarvenm na kdlech a ocasu. Hlava je charakteristicky bl, co dodv orlovi skalnmu vrazn a impozantn vzhled. Jejich siln a ostr drpy jsou pizpsobeny pro chytn a dren koisti. Orel skaln je vrcholovm predtorem, kter se iv pedevm menmi savci, jako jsou zajci, sviti a jin drobn hlodavci. Me tak lovit ptky a nkdy se ivit mrinami. Jeho loveck techniky jsou zaloeny na vyuit vek a ostrho zraku, kter mu umouje identifikovat potenciln koist i na velk vzdlenosti. Orel skaln je tak znm svm hnzdnm chovnm. Orelsk pen jsou obvykle monogamn a mohou spolu zstat po mnoho let. Samice sn obvykle 1-3 vejce, kter inkubuje po dobu piblin 40-45 dn. Oba rodie se podlej na pi o mlata a zajiuj jim potravu a ochranu. Bohuel, populace orla skalnho je ohroena ztrtou pirozenho prosted, lovu a otravm. Ochrana tohoto druhu je dleit pro zachovn biodiverzity a udren rovnovhy v prodnch ekosystmech. Mezinrodn organizace pro ochranu prody (IUCN) oznauje orla skalnho jako tm ohroenho druhu. Orel skaln je ikonickm symbolem sly, svobody a divokho prodnho prosted. Jeho ptomnost v prod je dleit a inspiruje ns k ochran a zachovn na prody pro budouc generace.",
