        description_long: "Sovice snn (Bubo scandiacus), tak znm jako polrn sova, je ndhern druh sovy, kter obv arktick oblasti Severn Ameriky, Evropy a Asie. Je to jedna z nejvtch sov a m sv charakteristick bl pe, kter j pomh splvat s prostedm snhu a ledu. Sovice snn m velkou hlavu, lut oi a vrazn ern okonice, kter j dodvaj neobyejn vzhled. Jej pe je bl s ernmi znaky, kter slou jako maskovn ped potencilnmi predtory. Jej pe m tak hustou a mkkou strukturu, co j pomh udrovat teplo v chladnch arktickch podmnkch. Tento druh sovy je specializovan na lov drobnch hlodavc, jako jsou piuchy a myi, kter jsou hlavnm zdrojem potravy. Sovice snn m vynikajc sluch a zrak, kter j umouj lokalizovat koist i ve velkch vzdlenostech. Me se sm dostat do vysokch letovch vek a pak se na svou koist snst s rychlost a pesnost. Co se te rozmnoovn, sovice snn vytv hnzda na zemi nebo na skalnch vstupech. Samice klade obvykle 3-9 vajec, kter inkubuje po dobu piblin 32-34 dn. Po vylhnut mlata zstvaj s matkou a jsou krmena a peovna o n po dobu nkolika tdn, ne jsou dostaten siln a schopn se samostatn ivit. Sovice snn je jedinenm a fascinujcm druhem, kter pev v extrmn chladnch podmnkch arktickho prosted. Nicmn, stejn jako mnoho dalch druh, je tak ohroena zmnou klimatu, ztrtou pirozench stanovi a ruenm lovkem. Je dleit chrnit tyto ndhern sovy a jejich prosted, aby peily a mohly pokraovat v ekosystmech Severnho polrnho regionu.",
