        description_long: "Zobroroec vrsit (Buceros bicornis) je velk a npadn ptk, kter obv tropick lesy jihovchodn Asie. Je to jedna z nejvtch zoborocovitch ptk a vyznauje se svm vraznm zobkem a vraznm hebenem na hlav. Zobroroec vrsit je znm svm masivnm zobkem, kter je dlouh a 100 cm u samc. Zobk je zahnut a m na konci charakteristickou vrsitou strukturu, kter mu dala jmno. Zobk je ern s lutou bz a je velmi siln, co mu umouje manipulovat s potravou a vyhlubovat si hnzdn dutiny ve stromech.  Samec zobroroce vrsitho je vraznj ne samice. M vt heben na hlav, kter je zbarven vraznou kombinac ern, bl a erven barvy. Samice m men heben a zobk, ale stle se vyznauje impozantnm vzhledem.  Tito ptci jsou pevn ovocnky, piem jejich strava zahrnuje irokou klu ovoce, bobul a semnek. Krom toho se tak iv hmyzem, plazy a menmi obratlovci. Jsou to aktivn letci a jejich siln kdla jim umouj pohybovat se mezi stromy a hledat potravu. Zobroroci vrsit jsou monogamn ptci a vytvej stabiln pry. Hnzd ve vyhloubench dutinch strom a samice klade obvykle jedno a ti vejce. O inkubaci se star oba rodie a po vylhnut mlat se podlej na jejich krmen a ochran. Bohuel, zobroroec vrsit je ohroen druh, pedevm kvli ztrt pirozenho prosted a nekontrolovanmu lovu. Je chrnn zkony a mezinrodnmi dohodami, ale ochrana jeho ivotnho prosted je klov pro dlouhodob zachovn tohoto ohroenho druhu. Zobroroec vrsit je nejen impozantnm ptkem s nezamnitelnm vzhledem, ale tak dleitm prvkem ekosystmu tropickch les.",
