import React, { useState, useEffect } from "react";
import animals from "../data/animals";
import "./Zvirata.scss";
import { Link } from "react-router-dom";
import emptyStar from "../images/icon/emptyStar.png";
import fullStar from "../images/icon/fullStar.png";

const Zvirata = () => {
  const [filter, setFilter] = useState(null);
  const [searchBox, setSearchBox] = useState("");
  const [filteredAnimals, setFilteredAnimals] = useState([]);
  const [activeButton, setActiveButton] = useState(false);
  const [favorite, setFavorite] = useState([]);

  // Zjist jestli je v oblbench
  const isFavorite = (id) => {
    return favorite.includes(id);
  };

  // Odstarn z obbench
  const removeFromFavorites = (id) => {
    const updatedFavorite = favorite.filter((favId) => favId !== id);
    setFavorite(updatedFavorite);
    localStorage.setItem("favorite", JSON.stringify(updatedFavorite));
  };

  // uloen do oblbench a do local storage
  const addToFavorites = (id) => {
    // Pidn pokud to id tam u nen
    if (!favorite.includes(id)) {
      setFavorite([...favorite, id]);

      // Uloit aktualizovan pole favorite do Local Storage
      localStorage.setItem("favorite", JSON.stringify([...favorite, id]));
    }
  };

  useEffect(() => {
    // Zjitn zda v local storage je favorite
    const existFavorite = localStorage.getItem("favorite");

    if (existFavorite) {
      setFavorite(JSON.parse(existFavorite));
    } else {
      setFavorite([]);
    }
  }, []);

  // Funkce pro aktivn button
  const activeBtn = (kind) => {
    setActiveButton(kind);
  };

  // Funkce pro scrollUp po kliknut
  const ScrollTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  // Funkce kter pjm z databze druh a a nastavuje ho do filtru
  const filterAll = (kind) => {
    setFilter(kind);
    // Pokud je filtrovno druhem nastav vechny filtry z inputu na nic
    setSearchBox("");
    activeBtn(kind);
  };

  const inputSearch = (e) => {
    e.preventDefault();
    // Pi sputn se zru ostatn filtry
    setFilter(null);

    // uloen vyhledvacho polka
    setSearchBox(e.target.value);
  };

  // Do filter se ulo hodnota z tlatka  a spust se filter kter vyfiltruje z databze jen pslun druhy a ulo je do profiltrovanho pole kter se bude vypisovat do strnky
  useEffect(() => {
    const filtered = animals.filter((one) => {
      // Filter podle druhu, pokud se druh a zrove druh v databz nerovanj vrz se false a druh se nevype
      if (filter && one.kind !== filter) {
        return false;
      }

      // Vyhledvn podle nzvu
      return one.name.toLowerCase().includes(searchBox.toLowerCase());
    });
    setFilteredAnimals(filtered);
  }, [filter, searchBox]);

  // Jet jednou aplikuje styly na pozad kter mi jinak nereagovali na konci
  useEffect(() => {
    const animalBoxes = document.getElementsByClassName("animal-box");

    for (let i = 0; i < animalBoxes.length; i++) {
      animalBoxes[i].style.backgroundPosition = "center";
      animalBoxes[i].style.backgroundSize = "cover";
    }
  }, [filteredAnimals]);

  return (
    <div className="zoo-animal">
      <h1>Zvata v na Svtov Zoo</h1>
      <form>
        <input value={searchBox} onChange={inputSearch} type="text" />
      </form>

      {/* Tlatka pro filtrovn */}
      <div className="button-container-animal">
        <button
          className={activeButton === null ? "active-button-animal" : ""}
          onClick={() => {
            filterAll(null);
            activeBtn(null);
          }}
        >
          Ve
        </button>
        <button
          className={activeButton === "Savec" ? "active-button-animal" : ""}
          onClick={() => {
            filterAll("Savec");
            activeBtn("Savec");
          }}
        >
          Savci
        </button>
        <button
          className={activeButton === "Ptk" ? "active-button-animal" : ""}
          onClick={() => {
            filterAll("Ptk");
            activeBtn("Ptk");
          }}
        >
          Ptci
        </button>
        <button
          className={activeButton === "Plaz" ? "active-button-animal" : ""}
          onClick={() => {
            filterAll("Plaz");
            activeBtn("Plaz");
          }}
        >
          Plazi
        </button>
        <button
          className={
            activeButton === "Obojivelnk" ? "active-button-animal" : ""
          }
          onClick={() => {
            filterAll("Obojivelnk");
            activeBtn("Obojivelnk");
          }}
        >
          Obojivelnci
        </button>
      </div>
      {/* Vpis do strnky */}
      <div className="zoo-animal-container">
        {filteredAnimals.map((one) => (
          <div
            onClick={() => ScrollTop()}
            to={`/zvirata/${one.id}`}
            key={one.id}
            className="animal-box"
            style={{ background: `url(${one.url})` }}
          >
            <div className="box">
              <div className="up-box">
                <h2>{one.name}</h2>
                {/* po klikntu zjisti zda je oblben, pokud je odstranit pokud ne pidat */}
                <div
                  onClick={() => {
                    isFavorite(one.id)
                      ? removeFromFavorites(one.id)
                      : addToFavorites(one.id);
                  }}
                  className="favourite"
                >
                  {/* Pokud toto id je oblben dt podle toho hvzdy */}
                  <img src={isFavorite(one.id) ? fullStar : emptyStar} alt="" />
                </div>
              </div>

              <Link to={`/zvirata/${one.id}`} className="box-down"></Link>

              <Link>Podpoit</Link>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Zvirata;
