import React, { useState, useRef } from "react";
import { Link } from "react-router-dom";
import "./ActualityForm.scss";
import Aktuality from "../pages/Aktuality";

const ActualityForm = () => {
  const [date, setDate] = useState("");
  const [heading, setHeading] = useState("");
  const [text, setText] = useState("");
  const refSubmitButton = useRef("");

  // Uloen hodnot z polek

  const saveDate = (e) => {
    setDate(e.target.value);
  };
  const saveHeading = (e) => {
    setHeading(e.target.value);
  };
  const saveText = (e) => {
    setText(e.target.value);
  };

  // co se stane po odesln formule
  const submitForm = (e) => {
    e.preventDefault();

    // Oven e jsou pole vyplnn
    if (!date || !heading || !text) {
      alert("Vechna pole mus bt vyplnna");
      return;
    }

    // Zskn existujcho pole z Local storage nebo jeho vytvoen
    const existingArray = JSON.parse(localStorage.getItem("actuality")) || [];

    // Vezme vechna existujc a pid k nim nov zznamy
    const updateArray = [...existingArray, { date, heading, text }];

    // Uloen aktualizovanho pole do local storage
    localStorage.setItem("actuality", JSON.stringify(updateArray));

    // promazn polek
    setDate("");
    setHeading("");
    setText("");

    refSubmitButton.current.innerText = "spn odeslno";
  };

  return (
    <div className="admin-page row">
      <div className="add-actuality">
        <div className="add-actuality-container">
          <h2>
            Prostor pro pidn aktulnho dn v zoo/ propisuje se do strnky
            aktuality <Link to="/aktuality">zde</Link>
          </h2>
          <form onSubmit={submitForm}>
            <div>
              <input
                type="text"
                onChange={saveDate}
                value={date}
                placeholder="Datum"
              />
              <input
                type="text"
                onChange={saveHeading}
                value={heading}
                placeholder="Nadpis"
              />
            </div>
            <input
              onChange={saveText}
              value={text}
              placeholder="Cel text aktualizace"
            ></input>

            <button ref={refSubmitButton} type="submit">
              Odeslat
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default ActualityForm;
