import React from 'react'
import "./OpenClose.scss"
import OpenCloseTime from "../data/DataOpenCloseTime"

const OpenClose = () => {
  return <div className="open-close row3">
        <h1>Otevrac doba</h1>
        <p>Zde najdete celoron pehled otevrac doby.</p>
        <p>Oteveno Kad den v roce</p>
        <p>(vedn dny, vkendy  i svtky)</p>
        
        <div className="container-time">
                <div className="heading-time">
                    <p>Msc</p>
                    <p>Doba Pokladny</p>
                    <p>Bistro</p>
                    <p>Uzaven pavilon</p>
                    <p>Uzaven arelu</p>
                    <p>Cennk</p>
                </div>

                {
                    OpenCloseTime.map( (one) => {
                        const {month, CashRegister, foot, closePavilon, closeZoo, priceList}  = one
                        return <div className='box-time' >
                            <p ><span>Msc</span> <br /> {month}</p>
                            <p ><span>Doba Pokladny</span> <br /> {CashRegister}</p>
                            <p ><span>Bistro</span> <br /> {foot}</p>
                            <p ><span>Uzaven Pavilon</span> <br /> {closePavilon}</p>
                            <p ><span>Uzaven arelu</span> <br /> {closeZoo}</p>
                            <p ><span>Cennk</span> <br /> {priceList}</p>
                        </div>
                    })
                }
        </div>
  </div>
}

export default OpenClose
