import React from "react";
import Prohibitions from "../components/Prohibitions";
import DailyProgram from "../components/DailyProgram";
import Background from "../components/Background";
import "./Home.scss";
import Faq from "../components/Faq";
import bg1 from "../images/home-foto/1.jpg";
import bg2 from "../images/home-foto/2.jpg";
import bg3 from "../images/home-foto/3.jpeg";
import bg4 from "../images/home-foto/4.jpg";
import bg5 from "../images/home-foto/5.jpg";
import bg6 from "../images/home-foto/6.jpg";
import { Link } from "react-router-dom";

const Home = () => {
  return (
    <div className="home-page">
      <Background />
      <div className="home-conatainer">
        <h1>Vtejte v Na Svtov Zoo</h1>
        {/* One Section */}
        <section>
          <div
            style={{
              background: `url(${bg1})`,
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
            }}
            className="left"
          ></div>
          <div className="right">
            <h2>Zvata v Zoo</h2>
            <p>
              Na ploe 14 ha mete obdivovat pes 170 druh zvat (1438
              zvat) ze vech kontinent. Mezi nejzajmavj chovan druhy
              pat takin nsk, zebra bezhv a sob lesn, levhart snn,
              margay, pavin pltkov a mangabej lutobich, viskaa, dracna
              krokodlovit nebo ara hyacintov a orlosup bradat. Zcela
              mimodn je nae kolekce horskch kopytnk a dravch ptk.
            </p>
            <Link to="/zvirata">Nae Zvata</Link>
          </div>
        </section>

        {/* One Section */}
        <section>
          <div
            style={{
              background: `url(${bg2})`,
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
            }}
            className="left"
          ></div>
          <div className="right">
            <h2>Vzcn druhy zvat a jejich ochrana</h2>
            <p>
              Vzcn druhy zvat jsou ohroenmi tvory, kter hraj dleitou
              roli v ekosystmech na planety. Pat sem napklad tygi,
              pandy, nosoroci a ledn medvdi. Tyto druhy jsou vystaveny rznm
              hrozbm, jako je ztrta pirozenho prosted, nelegln obchod se
              zvaty a lov. Ochrana tchto druh je nezbytn pro udren
              biologick rozmanitosti a ekologick stability. Organizace po
              celm svt se sna chrnit tyto vzcn druhy prostednictvm
              vzdlvn, ochrany prodnch rezervac a regulace obchodu se
              zvaty. Je dleit si uvdomit, e zachovn tchto druh je
              zodpovdnost ns vech. Spolenmi silmi meme zajistit, e
              tyto vzcn druhy budou mt anci pet pro dal generace a
              pispt k bohatstv na planety.
            </p>
            <Link to="/zvirata">Vzcn druhy zvat</Link>
          </div>
        </section>

        {/* One Section */}
        <section>
          <div
            style={{
              background: `url(${bg3})`,
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
            }}
            className="left"
          ></div>
          <div className="right">
            <h2>Pomhme chrnit produ</h2>
            <p>
              Ochrana prody je nezbytn pro zachovn ekosystm, biodiverzity
              a udritelnosti na planety. Zahrnuje snahy o ochranu ohroench
              druh, obnovu prodnch prosted a udriteln vyuvn
              prodnch zdroj. Ochrana prody je dleit pro udren
              ekologick rovnovhy, klimatickou stabilitu a prospch pro budouc
              generace. Je nezbytn pijmat individuln i kolektivn opaten,
              jako je omezen zneiovn, udriteln zemdlstv a lesnictv,
              ochrana prodnch rezervac a podpora ekoturistiky. Ochrana
              prody je na odpovdnost a investic do budoucnosti na
              planety a vech jejch obyvatel.
            </p>
            <Link to="/ochrana">Ochrana prody</Link>
          </div>
        </section>

        {/* One Section */}
        <section>
          <div
            style={{
              background: `url(${bg4})`,
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
            }}
            className="left"
          ></div>
          <div className="right">
            <h2>Mlata narozen v na Zoo</h2>
            <p>
              Mlata narozen v zoo pinej radost a nadji. Jsou dleitou
              soust ochrany ohroench druh. Zoo se sna poskytnout
              bezpen prosted pro rozmnoovn a pi o mlata. Tyto nov
              prstky pedstavuj nadji pro zachovn biodiverzity a oiven
              populac ohroench druh. Jsou zdrojem vzkumu a poznatk o
              chovn a potebch zvat. Mlata narozen v zoo pitahuj
              pozornost veejnosti a pomhaj vzdlvat o dleitosti ochrany
              prody. Jsou to symboly nadje a budoucch generac, kter nm
              pipomnaj, jak je dleit chrnit a respektovat ivotn
              prosted.
            </p>
            <Link to="/mladata">Nae Mlata</Link>
          </div>
        </section>

        {/* One Section */}
        <section>
          <div
            style={{
              background: `url(${bg5})`,
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
            }}
            className="left"
          ></div>
          <div className="right">
            <h2>Fotografovn naich zvat</h2>
            <p>
              Fotografovn zvat je umnm zachytit krsu, slu a jedinenost
              ivoinho svta. Vyaduje trplivost, znalosti a respekt k
              prod. Fotografie zvat nm umouj poznat a ocenit jejich
              rozmanitost a asn chovn. Pi fotografovn zvat je dleit
              bt ohledupln a nezasahovat do jejich pirozenho prosted.
              Respektujme jejich prostor a neovlivujme jejich chovn. Bt v
              souladu s prodou nm umon zachytit autentick a emotivn
              okamiky. Fotografovn zvat nm dv monost sdlet jejich
              krsu a inspirovat dal lidi k ochran prody.
            </p>
            <Link to="/foto">Fotogalerie</Link>
          </div>
        </section>

        {/* One Section */}
        <section>
          <div
            style={{
              background: `url(${bg6})`,
              backgroundPosition: "center",
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
            }}
            className="left"
          ></div>
          <div className="right">
            <h2>Jste vdy vtan</h2>
            <p>
              Zoo jsou oteveny cel rok, aby poskytovaly nvtvnkm monost
              prozkoumat a objevovat ivoin svt po cel rok. I kdy nkter
              zvata preferuj urit ron obdob, mnoho z nich je aktivnch i
              bhem zimy. Zoo tak pispvaj k vzdlvn veejnosti o ochran
              ivotnho prosted a ochran ohroench druh. Provoz zoo po cel
              rok tak zajiuje pi a odbornou podporu zvatm bez ohledu na
              seznu. Otevenost cel rok poskytuje lidem pleitost vytvet
              silnj vazby s ivoichy a zvyuje povdom o dleitosti
              ochrany prody.
            </p>
            <Link to="/open-close">Otevrac doba</Link>
          </div>
        </section>
      </div>

      <DailyProgram />
      <Prohibitions />
      <Faq />
      {/* <Cennik/> */}
    </div>
  );
};

export default Home;
