import React from "react";
import "./Faq.scss";
import DataFaq from "../data/DataFaq";
import { useState } from "react";

const Faq = () => {
  const [selected, setSelected] = useState(null);

  const toggle = (index) => {
    if (selected === index) {
      // nejdv se zavou vechny odpovdi
      setSelected(null);
    } else {
      // ta se shodnm idexem se oteve
      setSelected(index);
    }
  };

  return (
    <div className="faq ">
      <div className="accordion row2">
        {DataFaq.map((one, index) => {
          return (
            <div className="itembox">
              <div className="title" onClick={() => toggle(index)}>
                <h2>{one.question}</h2>
                {/* Pokud je index shodn a oteve se nabdka zmn se + na - */}
                <span>{selected === index ? "-" : "+"}</span>
              </div>
              {/* Pi shodnm idexu se zmn classy */}
              <div className={selected === index ? "content show" : "content"}>
                {one.answer}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Faq;
